.white-theme {
  --content_text-color: #1e1e1e;
  --content_bg-color: white;

  --header_text-color: #363636;
  --header_bg-color: darkgray;

  --key_text-color: gray;
  --key_bg-color: transparent;
  --key_border: 1px solid gray;

  --right-key_text-color: #363636;
  --right-key_bg-color: #d4d4d4;
  --right-key_border: 1px solid #363636;

  --active-key_text-color: #2f3d4f;
  --active-key_bg-color: #6495ed;
  --active-key_border: 1px solid #2f3d4f;

  --wrong-key_text-color: brown;
  --wrong-key_bg-color: tomato;
  --wrong-key_border: 1px solid brown;

