
.Button button {
  transition: 0.2s ease-out;
  border: none;
  cursor: pointer;
  padding: 6px 10px;
  margin: auto;
  border-radius: 5px;
  font-size: 1.1rem;
  color: #d4d4d4;
  background-color: #191919;
}
.Button button:focus,
.Button button:hover {
  background-color: #d4d4d4;
  color: #1e1e1e;
}
